code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #212121;
  padding-top: 50px;
  color: white;
  text-align: center;
}
.Cell {
  height: 100px;
  width: 100px;
  border-radius: 1px;
  transition: background-color 0.5s ease;
  background-color: #263238;
}

.Cell-lit {
  background-color: #00bcd4;
}
.Board {
  margin: 0 auto;
  box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
  border-radius: 10px;
}

table tr:first-child td:first-child {
  border-radius: 10px 0 0 0 !important;
}

table tr:first-child td:last-child {
  border-radius: 0 10px 0 0 !important;
}

table tr:last-child td:first-child {
  border-radius: 0 0 0 10px !important;
}

table tr:last-child td:last-child {
  border-radius: 0 0 10px 0 !important;
}

.Board-winner {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Board-winner .neon,
.Board-winner .flux {
  font-size: 200px;
}

.Board-retry {
  margin: 20px auto;
}

.Board-button {
  color: white;
  background-color: #FF4500;
  padding: 0.5em 1em;
  border-radius: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 40px;
  outline: none;
  border: none;
}

.Board-button:hover {
  cursor: pointer;
  background-color: tomato;
}

.Board-button .fa-undo-alt {
  margin-left: 10px;
  font-size: 35px;
}

.Board-neons {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
}

@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.neon {
  font-family: neon;
  color: #FB4264;
  font-size: 80px;
  text-shadow: 0 0 3vw #F40A35;
  animation: neon 3s ease infinite;
  -moz-animation: neon 3s ease infinite;
  -webkit-animation: neon 3s ease infinite;
}

.flux {
  font-family: neon;
  color: #426DFB;
  font-size: 80px;
  text-shadow: 0 0 3vw #2356FF;
  animation: flux 2s linear infinite;
  -moz-animation: flux 2s linear infinite;
  -webkit-animation: flux 2s linear infinite;
  -o-animation: flux 2s linear infinite;
}

@keyframes neon {

  0%,
  100% {
    text-shadow: 0 0 1vw #FA1C16, 0 0 1vw #FA1C16, 0 0 1vw #FA1C16, 0 0 1vw #FA1C16, 0 0 1vw #FED128, .5vw .5vw .1vw #806914;
    color: #FED128;
  }

  50% {
    text-shadow: 0 0 .5vw #800E0B, 0 0 1.5vw #800E0B, 0 0 1vw #800E0B, 0 0 1vw #800E0B, 0 0 .2vw #800E0B, .5vw .5vw .1vw #40340A;
    color: #806914;
  }
}

@keyframes flux {

  0%,
  100% {
    text-shadow: 0 0 1vw #1041FF, 0 0 1vw #1041FF, 0 0 1vw #1041FF, 0 0 1vw #1041FF, 0 0 1vw #8BFDFE, .5vw .5vw .1vw #147280;
    color: #28D7FE;
  }

  50% {
    text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 1vw #082180, 0 0 1vw #082180, 0 0 .2vw #082180, .5vw .5vw .1vw #0A3940;
    color: #146C80;
  }
}
.App {
  /* text-align: center;
  display: flex;
  align-items: center; */
  justify-content: center;
  height: 100vh;
}

#clock {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 70px;
}
